body {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: #3f4850;
}

h2 {
  font-size: 2em;
  margin: 52px 0 40px;
  text-transform: capitalize;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}
header {
  background-color: rgba(67,139,189, .5);
  border-radius: 4px 4px 0px 0px;
}

footer {
  background-color: rgba(67,139,189, .5);
  border-radius: 0px 0px 4px 4px;
}

.search-form {
  max-width: 460px;
  display: flex;
  margin: 0 auto 32px;
}

.search-form button {
  outline: none;
  background-color: #438bbd;
  border: none;
  padding: 0px 15px;
  border-radius: 0 0.35em 0.35em 0;
  cursor: pointer;
}

input {
  font-size: 1em;
  width: 100%;
  background-color: #edeff0;
  padding: 10px 15px;
  border: 3px solid #d7dbdf;
  border-right: none;
  border-radius: 0.35em 0 0 0.35em;
  outline: none;
}

.main-nav li {
  width: 100%;
  margin-bottom: 1em;
}

.main-nav a {
  display: block;
  background: #438bbd;
  border-radius: 3px;
  padding: 5px;
  color: #fff;
}

.main-nav .active,
.main-nav a:hover,
.search-form button:hover {
  background-color: #275270;
}

.photo-container ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  -ms-align-items: flex-start;
  align-items: flex-start;
}

.photo-container ul li {
  width: 220px;
  height: 165px;
  overflow: hidden;
  margin-bottom: 15px;
}

.photo-container ul img {
  width: 100%;
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  transition: -webkit-transform 1.25s;
  transition: transform 1.25s;
  transition: transform 1.25s, -webkit-transform 1.25s;
}

.photo-container li:hover img {
  -webkit-transform: scale(1.65);
          transform: scale(1.65);
}

.not-found {
  width: 100%;
  color: #275270;
}

.icon {
  transition: 
    -webkit-transform .5s ease-in,
    -webkit-filter .5s ease-in;
  transition: 
    transform .5s ease-in,
    filter .5s ease-in;
  transition: 
    transform .5s ease-in,
    filter .5s ease-in,
    -webkit-transform .5s ease-in,
    -webkit-filter .5s ease-in;
}

.icon:hover {
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
  -webkit-filter: invert(.5) sepia(1) saturate(1.6) brightness(85%) contrast(150%) hue-rotate(160deg);
          filter: invert(.5) sepia(1) saturate(1.6) brightness(85%) contrast(150%) hue-rotate(160deg);
}

/********* Media Queries ********/

@media only screen and (max-width: 767px)  {
  body {
    padding-top: 2em;
  }

  .photo-container li {
    margin: auto;
  }
}

@media only screen and (min-width: 768px)  {
  body {
    padding-top: 4em;
  }

  .container {
    max-width: 960px;
    margin: auto;
  }

  .main-nav ul {
    display: flex;
    justify-content: center;
  }
  
  .main-nav li {
    margin: 10px;
    width: 100px;
  }
}

